.container {
    width: 100%;
    height: 100%;
    opacity: 15%;
    display: flex;
    flex-direction: column;
    gap: 5px;
    justify-content: center;
    align-items: center;
    user-select: none;
  }
  
  .container__img {
    width: 10%;
    height: 10%;
    object-position: center;
    object-fit: contain;
  }
  