.container {
    width: 20%;
    display: flex;
    flex-direction: column;
    background-color: var(--colors-bg);
    padding: calc(var(--ml-container) / 3);
  }
  .container h3 {
    font-size: 18px;
    font-weight: 600;
    margin-top: 30px;
    margin-bottom: 10px;
  }
  
  .container ol li {
    list-style-type: none;
    margin: 0.3rem 0;
  }
  
  @media (max-width: 1024px) {
    .container {
      display: none;
    }
  }
  